import React, { Component } from "react";

import "./ClientList.css";

import { PopContext } from "./PopContext";

class ClientList extends Component {
	state = {};

	render() {
		return (
			<React.Fragment>
				<PopContext.Consumer>
					{context => (
						<div className="client-list">
							{context.state.clients.map(client => {
								return (
									<div key={client.id} className="client-name">
										{client.name}
									</div>
								);
							})}
						</div>
					)}
				</PopContext.Consumer>
			</React.Fragment>
		);
	}
}

export default ClientList;
